button {
  display: block;
  padding: 10px;
  outline: none;
  border: none;
  background-color: #fff;
  color: #0384fd;
  margin: 10px 30px;
  cursor: pointer;
  border-radius: 5px;
  width: 200px;
  font-size: 1rem;
  font-weight: 900;
}

.next-button {
  margin-top: 10%;
  color: #fff;
  background-color: #0384fd;
}
.next-button:hover {
  background-color: #fff;
  color: #0384fd;
}
.question,
.result {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
select {
  width: 100%;
  height: 1.7rem;
  border: none;
  text-align: center;
  outline: none;
  font-size: 13px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
option {
  font-size: 13px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
